            "Formação clínica que pretende pensar os fundamentos conceptuais e metodológicos da WISC-III e sua respetiva tradução para a prática da avaliação psicológica e interpretação clínica.",
          info: "Outras informações relacionadas com Escala de inteligência para crianças |WISC-III|: Aplicação, avaliação e interpretação clínica",
          img_src: icon,
          link: "/formacoes/escala-de-inteligencia-para-criancas-wisc-iii-aplicacao-avaliacao-interpretacao-clinica"
        },
        {
          id: 4,
          code: "introducao-as-tecnicas-projetivas",
          title: "Introdução às Técnicas Projetivas",
          text:
            "Formação clínica que pretende apresentar os fundamentos teóricos e metodológicos do Teste Apercetivo de Roberts para Crianças (R.A.T.C), do Teste de Aperceção Infantil (C.A.T.-A) e do Teste de aperceção temática (T.A.T) e sua aplicação na prática clínica.",
          info: "Outras informações relacionadas com Introdução às Técnicas Projetivas",
          img_src: img,
          link: "/formacoes/introducao-as-tecnicas-projetivas"
        }
      ]
    };
