//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.formacoes-wrapper {
  display: flex;
}

.formacoes {
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;

  @media only screen and (max-width: 991px) {
    display: none;
  }

  .row {
    height: pxToEm(530);
    padding: 0 pxToEm(130);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

