          top: 50%;
          transform: translateY(-50%);
          left: 0;
          padding: pxToEm(40) pxToEm(60);
          transition: 0.5s;

          .copy {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: pxToEm(200);

            .title {
              font-size: pxToEm(26);
              color: $turquiose;
              font-family: "OS-Regular";
              font-weight: bold;
            }

            .line {
              width: pxToEm(80);
              height: pxToEm(1);
              background-color: $turquiose;
              margin-top: pxToEm(10);
            }

            .text {
              font-size: pxToEm(20);
              color: #707070;
              line-height: 1.5em;
              margin-top: pxToEm(10);
              font-family: "OS-Regular";
              margin-top: pxToEm(20);
            }

            .btn {
              width: pxToEm(131);
              height: pxToEm(44);
              align-self: flex-end;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                margin-bottom: pxToEm(20);
              }

              .txt {
                font-family: Arial, Helvetica, sans-serif;
                font-size: pxToEm(14);
                font-weight: bold;
              }
            }
          }
        }

        .right {
          width: pxToEm(590);
          height: pxToEm(290);
          background-color: $turquiose;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          padding: 0;
          box-shadow: none;
          border-radius: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .txt {
            color: $white;
            font-size: pxToEm(20);
            font-family: "OS-Bold";
            text-align: center;
            max-width: 90%;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: #fff;
            margin-top: pxToEm(10);
          }
        }
      }
    }

    &:nth-child(odd) {
      background-color: #fff;

      .wrapper {
        max-width: pxToEm(1490);
        width: 100%;
        height: 100%;
        margin: auto;
        position: absolute;

        .left {
          width: pxToEm(590);
          height: pxToEm(290);
          background-color: $turquiose;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          border-radius: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .txt {
            color: $white;
            font-size: pxToEm(20);
            font-family: "OS-Bold";
            text-align: center;
            max-width: 90%;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: #fff;
            margin-top: pxToEm(10);
          }
        }

        .right {
          position: absolute;
          width: pxToEm(770);
          // height: auto;
          min-height: pxToEm(200);
          background-color: #fff;
          border-radius: pxToEm(10);
          box-shadow: pxToEm(10) pxToEm(11) pxToEm(17) rgba(0, 0, 0, 0.1);
          top: 50%;
          transform: translateY(-50%);
          right: 0;
