            .title {
              font-size: pxToEm(26);
              color: $turquiose;
              font-family: "OS-Regular";
              font-weight: bold;
            }

            .line {
              width: pxToEm(80);
              height: pxToEm(1);
              background-color: $turquiose;
              margin-top: pxToEm(10);
            }

            .text {
              font-size: pxToEm(20);
              color: #707070;
              line-height: 1.5em;
              margin-top: pxToEm(10);
              font-family: "OS-Regular";
              margin-top: pxToEm(20);
            }

            a {
              align-self: flex-end;
            }

            .btn {
              width: pxToEm(131);
              height: pxToEm(44);
              align-self: flex-end;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                margin-bottom: pxToEm(20);
              }

              .txt {
                font-family: Arial, Helvetica, sans-serif;
                font-size: pxToEm(14);
                font-weight: bold;
              }
            }
          }
        }
      }
    }
  }
