        height: auto;
        transition: 0.5s;
        padding: pxToEm(40) 0;
        display: flex;
        flex-direction: column;

        .copy {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          .title {
            font-size: pxToEm(26);
            color: $turquiose;
            font-family: "OS-Regular";
            font-weight: bold;
            text-align: center;
