            width: pxToEm(80);
            height: pxToEm(1);
            background-color: $turquiose;
            margin-top: pxToEm(10);
          }

          .text {
            font-size: pxToEm(20);
            color: #707070;
            line-height: 1.5em;
            margin-top: pxToEm(10);
            font-family: "OS-Regular";
            margin-top: pxToEm(20);
            text-align: center;
          }

          .btn {
            width: pxToEm(131);
            height: pxToEm(44);
            margin-top: pxToEm(50);

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              margin-bottom: pxToEm(20);
            }

            .txt {
              font-family: Arial, Helvetica, sans-serif;
              font-size: pxToEm(14);
              font-weight: bold;
            }
          }
        }
      }

      .bottom {
        width: 100%;
        height: auto;
        background-color: $turquiose;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: pxToEm(20) 0;

        .txt {
          color: $white;
          font-size: pxToEm(20);
          font-family: "OS-Bold";
          text-align: center;
        }
      }
    }
  }
}
</style>



