<template>
  <div>
    <banner></banner>
    <apresentacao></apresentacao>
    <formacoes></formacoes>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./formacoes/Banner";
import Apresentacao from "./formacoes/Apresentacao";
import Formacoes from "./formacoes/Formacoes";

export default {
  components: {
    banner: Banner,
    apresentacao: Apresentacao,
    formacoes: Formacoes
  },
  metaInfo() {
    return {
      title: "Formações - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content: "Formações - Ana Carolina Pereira - Clínica de Psicologia"
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
</style>



